import React, { useContext } from 'react'
import styled from 'styled-components'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import themeGet from '@styled-system/theme-get';

import Container from '../../components/Container'
import Box from '../../components/Box'
import Flex from '../../components/Flex'
import Source from '../../components/Source'
import Text from '../../components/Text'
import Title from '../../components/Title'
import Button from '../../components/Button'

import useResponsive from '../../contexts/mediaQuery/useResponsive'
import fundInfoContext from '../../contexts/fundInfo/context'

import paper from './paper.svg'
import Allocation from '../Charts/Allocation'
import Price from '../Charts/Price'
import PriceTable from '../Charts/PriceTable'
import USDividendsTable from '../Charts/USDividendsTable'
import SADividendsTable from '../Charts/SADividendsTable'

const StyleTab = styled(Tab)`
  width: ${props => props.width * 100}%;
  cursor: pointer;
  font-size: 1.428em;
  font-weight: bold;
  padding: 1em 1em;
  border-radius: 0;
  display: inline-block;
  text-align: center;
  background-color: ${themeGet('colors.lightGray')};
  transition: all ${themeGet('duration', 250)}ms;
  &:hover,
  &.react-tabs__tab--selected {
    background-color: ${themeGet('colors.prussianBlue')};
    color: ${themeGet('colors.ceruleanBlue')};
  }
`

const StyleTabList = styled(TabList)`
  border-bottom: none;
  padding: 0;
`

const Fund = () => {
  const { isMobile } = useResponsive()
  const { activeTab, setActiveTab } = useContext(fundInfoContext)
  const sets = [
    {
      name: '',
      data: [
        {
          title: '',
          Comp: (props) => <Allocation {...props} index={0} />,
          source: '2020/01/31'
        },
        {
          title: '',
          Comp: (props) => <Allocation {...props} index={1} />,
          source: '2020/01/31'
        },
      ]
    },
    {
      name: '',
      data: [
        {
          Comp: PriceTable,
          source: ' Lipper, Schroders, A2020/4/30Lipper GlobalBond Global USD (%):/*12() Bloomberg Barclays Multiverse ex Tr A+to B-TR USDH'
        },
        {
          title: '',
          Comp: Price,
          source: '2020/01/31'
        },
      ]
    },
    {
      name: '',
      data: [
        {
          Comp: USDividendsTable,
        },
        {
          Comp: SADividendsTable,
          source: 'Lipper, Schroders, 2020/3/312016/11/30(%):/*12()'
        },
      ]
    },
  ]

  return (
    <Box>
      <Title src={paper} title="" />
      <Container py={['2.86em', null, '7.143em']}>
        <Tabs selectedIndex={activeTab} onSelect={setActiveTab}>
          <StyleTabList>
            {sets.map(({ name }) => (
              <StyleTab key={name} width={isMobile ? 1 : (1 / sets.length)}>{name}</StyleTab>
            ))}
          </StyleTabList>
          <div id="fund-data">
            <Text pt="5rem" fontWeight="bold" fontSize={['1.43em', null, '2em']} color="prussianBlue">()</Text>
            <Text fontSize={['1.43em', null, '2em']} color="prussianBlue">()</Text>
            {sets.map(({ data }, i) => (
              <TabPanel key={i}>
                {data.map(({ title, Comp, source }, k) =>(
                  <Box key={k}>
                    <Text fontWeight="bold" fontSize={['1.285em', null, '1.57em']} pt="8rem" pb="3rem">{title}</Text>
                    <Comp isMobile={isMobile} />
                    {source && <Source>{source}</Source>}
                  </Box>
                ))}
              </TabPanel>
            ))}
          </div>
        </Tabs>
      </Container>
    </Box>
  )
}

export default Fund
